import { Box, Button, Container, Typography } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'

const AddPostInfo = () => {
  return (
    <Box sx={{ minHeight: '80vh', marginTop: '6rem', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
        <Typography>Tietoa postauksen lisyksest</Typography>
        <Container sx={{ margin: '1rem', padding: '1rem', backgroundColor: '#222222', textAlign: 'center', borderRadius: '0.5rem' }}>
          <Typography>Etsitk projektillesi tekij?</Typography>
          <Button component={Link} to='/lisaailmoitus/etsitekijaa'>Lis tekolyprojektipyynt</Button>
          <Typography>Oletko kehittj?</Typography>
          <Button component={Link} to='/lisaailmoitus/kehittajanilmoitus'>Lis tekolyprojekti kehittjn</Button>
          <Typography>Haluatko list julkaisun feediin?</Typography>
          <Button component={Link} to='/lisaajulkaisu'>Lis julkaisu Feediin</Button>
        </Container>
    </Box>
  )
}

export default AddPostInfo