import { Box, Typography, TextField, Button } from '@mui/material'
import React from 'react'
import { useSelector } from 'react-redux'
import { useDispatch } from 'react-redux'
import { useNotification } from '../../hooks'
import { useState } from 'react'
import { updateUser } from '../../reducers/user'

const ModifyBasicInfo = () => {
    
    const localUser = useSelector(({user}) => user)
    const user = useSelector(({users}) => users).find(u => u.id === localUser.id)
  
    const [email, setEmail] = useState(user.email ? user.email : '')

    const notify = useNotification()
    const dispatch = useDispatch()

    const handleSubmit = async () => {
        const confirmed = window.confirm(`Haluatko varmasti pivitt shkpostiosoitteeksi ${email}`)
    if (!confirmed) {
      return // If the user clicks "Cancel," do nothing
    }

        try {
            dispatch(updateUser({...user, email }))
            notify('Pivitys tehty onnistuneesti', 'success')
        } catch (error) {
            notify('Ilmeni jokin ongelma pivityksess, yrit myhemmin uudelleen', 'error')
        }
    }

      if (!user) {
          return null
      }

  return (
    <Box>
        <Typography>Nimi {user.name}</Typography>
        <Typography>Kyttjtunnus {user.username}</Typography>
        <Typography>Shkposti {user.email}</Typography>
        <Box sx={{ marginTop: '1rem', display: 'flex', flexDirection: 'row',
        flexWrap: 'wrap', alignItems: 'center'
            }}>
            <TextField
            id="email"
            label="Muokkaa shkpostiosoitetta"
            type='email'
            fullWidth
            value={email}
            onChange={({ target }) => setEmail(target.value)}
            sx={{ marginBottom: '1rem', maxWidth: '25rem' }}
        />
        <Button
            onClick={handleSubmit}
        >
            Pivit
        </Button>
      </Box>
    </Box>
  )
}

export default ModifyBasicInfo